	module.exports = {"pathContext":{"docHtml":"<h1>Change Log</h1>\n<p>All notable changes to this project will be documented in this file. See <a href=\"https://github.com/conventional-changelog/standard-version\">standard-version</a> for commit guidelines.</p>\n<p><a name=\"3.0.1\"></a></p>\n<h2><a href=\"https://github.com/tapjs/signal-exit/compare/v3.0.0...v3.0.1\">3.0.1</a> (2016-09-08)</h2>\n<h3>Bug Fixes</h3>\n<ul>\n<li>do not listen on SIGBUS, SIGFPE, SIGSEGV and SIGILL (<a href=\"https://github.com/tapjs/signal-exit/issues/40\">#40</a>) (<a href=\"https://github.com/tapjs/signal-exit/commit/5b105fb\">5b105fb</a>)</li>\n</ul>\n<p><a name=\"3.0.0\"></a></p>\n<h1><a href=\"https://github.com/tapjs/signal-exit/compare/v2.1.2...v3.0.0\">3.0.0</a> (2016-06-13)</h1>\n<h3>Bug Fixes</h3>\n<ul>\n<li>get our test suite running on Windows (<a href=\"https://github.com/tapjs/signal-exit/issues/23\">#23</a>) (<a href=\"https://github.com/tapjs/signal-exit/commit/6f3eda8\">6f3eda8</a>)</li>\n<li>hooking SIGPROF was interfering with profilers see <a href=\"https://github.com/tapjs/signal-exit/issues/21\">#21</a> (<a href=\"https://github.com/tapjs/signal-exit/issues/24\">#24</a>) (<a href=\"https://github.com/tapjs/signal-exit/commit/1248a4c\">1248a4c</a>)</li>\n</ul>\n<h3>BREAKING CHANGES</h3>\n<ul>\n<li>signal-exit no longer wires into SIGPROF</li>\n</ul>","docPath":"argo-ci/node_modules/signal-exit/changelog","proj":"argo-ci"}}

